import React from 'react'

function Constitution() {
  return (
    <>
    
    
    <main>
    <h1 class="page-hero-text">constitution of the befang cultural and development association</h1>
         <h2>preamble</h2>
         <p>We the people of Befang Village in order to form ourselves into an association to bring
             together all the elements of Befang origin and maintain contact among them with a view of 
             keeping alive and fostering the spirit of oneness and belongin that is a principal 
             characteristic of all developed and enlightened people do hereby solemnly associate and 
             constitute ourselves the Befang Cultural and Development Association and adopt this as our
             rules and regulations (constitution)</p>
         <h2>article 1: name</h2>
         <p>The association shall be called the Befang Cultural and Development Association to be
                abbreviated as BECUDA
         </p>
         <h2>article 2: headquarters</h2>
         <p>The headquaters shall be in Befang - Menchum Valleyy Sub Divisin - Menchum Division - 
            North West Region.
         </p>
         <h2>article 3: motto</h2>
         <p>The motto of BECUDA shall be Unity, Self-reliance, Culture and Development.</p>
         <h2>article 4: emblem</h2>
         <p>The emblem of the association shall be a palm tree with someone climbing it.</p>
         <h2>article 5: objectives</h2>
         <ul>
             <li>5.1 The objective of BECUDA shall be as per the preamble and in particular;</li>
             <li>a) To promote and maintain unity, brotherhood, peace and progress, amongst members.</li>
             <li>b) To revive, reinforce, enhance , advance, promote and maintain the rich culture of the
                    Befang people.
             </li>
                 
             <li>c)
                 To promote the social, moral and economic development in Befang and Cameroon at large.
            </li>
             <li>d)
                 To enhance the conception and realisation of grassroot projects in the village.
            </li>
             <li>e) To inspire pride in our native land and to contribute and encourage the pursuit of all the
                 other lawful activities capable of attianing the above mentioned objectives.
             </li>
         </ul>
         <h2>article 6: membership</h2>
         <ul>
             <li>6.1 Membership is opened to all Befang indigenes as well as to non Begang indigenes
                 interested in the goals and objectives of the association.
             </li>
             <li>
                6.2 There shall be two types of membership namely: active members and passive members.
                </li>
             <li>6.2 a: An active member shall be one who has paid his registration and anual contribution
                and takes active part in the association's business.
             </li>
             <li>
                6.2 b: Active members shall enjoy all the rights and privileges that go with
                membership of the association. 
             </li>
             <li>
                6.2 c: A passive member shall be one who has registered with BECUDA but has failed
                to pay his annual contributions and does not take active part in the association's 
                business.
             </li>
             <li>
                6.2 d: A passive member shall not during the year in which he has failed to pay his 
                annual contributions, have the right to vote and shall not stand for any office in 
                the association.
             </li>
             <li>
                6.2 e: A passive member shall become an active member upon payment of all the annual
                 contributuons due by him.
             </li><br/>
             <li>
                6.3 Upon registration with BECUDA, a member shall pay a non-refundable registration fee of 
                500cfa and a non-refundable annual contribution of 1.000cfa for members residing out of Befang
                village and 500cfa for members residing in Befang village.
             </li>
             <li><br/>
                6.4 Upon admission to membership of BECUDA, a member shall be issued a membership
                card signed by the incumbent president of his region.Membership cards will be terminated
                only by death or as shall otherwise be decided. 
             </li>
         </ul>
         <h2>article 7 : organization of the association</h2>
         <ul>
            <li>
               7.1 The association shall comprise the following funtional organs:
            </li>
            <li>
               7.1 a: The General Assembly
            </li>
            <li>
               7.1 b: The National Executive Council
            </li>
            <li>
               7.1 c: The Regional Executive Council
            </li>
            <li>
               7.1 d: The Branch Executive Council
            </li>
            <li>
               7.1 e: The Conference of Delegates
            </li><br/>
            <li className='text-bold'>
               7.2: Composition
            </li>
            <li>
               7.2 a: The General Assembly: The General Assembly shall be composed
               of all the registered members of the association.
            </li>
            <li>
               7.2 b: The National Executive Council.<br />  The National Executive Council
               shall comprise:
            </li>
            <li>
                1) The National President<br/> 2) Two Vice National Presidents, one of whom
                must be a lady.<br/> 3) The National Secretary<br/> 4) The Vice National 
                Secretary<br/> 5) The National Treasurer<br/> 6) The National Financial Secretary<br/>
                7) The Legal Adviser<br/> 8) The Special Adviser (2) <br/> 9) The National 
                Publicity Secretaries (2) <br/> 10) The National Social Secretaries (4) <br/>
                11) The National Auditors (2) <br/> 12) The National Youth Coordinators (2)
                13) The National Patrons and Matrons (4)
            </li><br/>
            <li>
               7.2 c: The Regional Executive Council <br/> The Regional Executive
               Council shall comprise:
            </li>
            <li>
                1) The National President<br/> 2) Two Vice National Presidents, one of whom
                must be a lady.<br/> 3) The National Secretary<br/> 4) The Vice National 
                Secretary<br/> 5) The National Treasurer<br/> 6) The National Financial Secretary<br/>
                7) The Legal Adviser<br/> 8) The Special Adviser (2) <br/> 9) The National 
                Publicity Secretaries (2) <br/> 10) The National Social Secretaries (4) <br/>
                11) The National Auditors (2) <br/> 12) The National Youth Coordinators (2)
                13) The National Patrons and Matrons (4)
            </li><br/>
            <li>
               7.2 d: The Branch Executive Council <br/> The Branch Executive
               Council shall comprise:
            </li>
            <li>
                1) The Branch President<br/> 2) The Vice Branch President.<br/> 3) The Branch Secretary<br/> 4) The Vice Branch 
                Secretary<br/> 5) The Branch Treasurer<br/> 6) The Branch Financial Secretary<br/>
                7) The Branch Publicity Secretary<br/> 8) The Branch Social Secretaries (2) <br/> 9) The Branch 
                Technical Advisers (2) <br/> 10) The Branch Auditor <br/>
                11)  The Branch Youth Coordinators (2) <br/> 12) The Branch Women's Coordinator <br/>
                13) The Branch Patrons and Matrons (2)
            </li>
            <li><br/>
               7.2 e: The Conference of Delegates <br/> The Conference of Delegates
               shall comprise:
            </li>
            <li>
                1) All the members of the national Exco<br/> 2) All the members of the regional Exco <br/> 3) All the members of the branch Exco
            </li> <br/>
            <li className='text-bold'>
                7.3: Functioning 
            </li>
            <li>
                7.3 a: The General Assembly 
           </li>
            <li>
                1) It shall be the supreme organ of the association. <br/>
                2) It shall have exclusive jurisdiction in the following matters: <br/>
                <span>- Amendment of the constitution</span><br/>
                <span>- The dissolution of the association and the subsequent disposal of its funds and properly if need be.</span><br/>
                
                <span>- Ratification and rectification of the actions/decisions taken by the national executive council.</span><br/>
                <span>- Approval of projects.</span> <br/>
                3) The General Assembly which is hereby denoted as the National Convention shall hold once a
                year in the village (or in any other venue as shall from time to time be decided by the members)
                on such date and time as shall be decided by the National Exco.
           </li><br/>
           <li>7.3 b: The National Council</li>
           <li>
            1) Members of the National Executive Council shall hold office for 3 calendar
           years and shall re-eligible once. <br/>
            2) The National Executive Council shall meet three times a year at such place,date and time as
            shall be decided by the president. <br/> 
            3) An extra-ordinary national Exco meeting may be summoned at such place, date and time
            as shall be decided by the president or 2/3 of the members of the said committee if such members find that things
            are not moving to the interest of the association and the president is reluctant to convene a meeting. <br/>

           </li>
         </ul> 
    </main>
      

    </>
  )
}

export default Constitution;